import { Component, OnInit } from '@angular/core';
import { Classes, WeaponGrid } from 'src/app/models/weapon.model';
import { WeaponListActions } from './../../state/weapon-list.actions';
import { WeaponGridActions } from './../../state/weapon-grid.actions';
import { WeaponGridState } from './../../state/weapon-grid.state';
import { Select, Store } from '@ngxs/store';
import { Observable } from 'rxjs';

@Component({
  selector: 'app-vanguard-home',
  templateUrl: './vanguard-home.component.html',
  styleUrls: ['./vanguard-home.component.css']
})
export class VanguardHomeComponent implements OnInit {
  @Select(WeaponGridState.getWeaponGrids) grids$: Observable<number>;

  classes = new Classes();

  constructor(private store: Store) { }

  ngOnInit(): void {
    this.store.dispatch(new WeaponListActions.LoadWeaponList());
  }
}
