import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { Weapon, WeaponDetailed } from 'src/app/models/weapon.model';

@Component({
  selector: 'app-weapon',
  templateUrl: './weapon.component.html',
  styleUrls: ['./weapon.component.css']
})
export class WeaponComponent implements OnInit {
  @Input() weapon: Weapon;
  @Input() rarity: string;
  @Input() isOnGrid: boolean;
  @Output() clickWeaponEmit = new EventEmitter();

  iconIndex: number = 0;

  constructor() {}

  ngOnInit(): void {
    if (this.weapon.rarity === 'A') {
      if (this.rarity === 'A' || this.rarity === '') {
        this.iconIndex = 0;
      } else if (this.rarity === 'S') {
        this.iconIndex = 1;
      } else if (this.rarity === 'SR') {
        if (this.weapon.icon.length > 2) {
          this.iconIndex = 2;
        } else {
          this.iconIndex = -1;
        }
      } else if (this.rarity === 'L') {
        this.iconIndex = -1;
      }
    } else if (this.weapon.rarity === 'S') {
      if (this.rarity === 'A') {
        this.iconIndex = -1;
      } else if (this.rarity === 'S' || this.rarity === '') {
        this.iconIndex = 0;
      } else if (this.rarity === 'SR') {
        this.iconIndex = 1;
      } else if (this.rarity === 'L') {
        this.iconIndex = -1;
      }
    } else if (this.weapon.rarity === 'SR') {
      if (this.rarity === 'A') {
        this.iconIndex = -1;
      } else if (this.rarity === 'S') {
        this.iconIndex = -1;
      } else if (this.rarity === 'SR' || this.rarity === '') {
        this.iconIndex = 0;
      } else if (this.rarity === 'L') {
        this.iconIndex = 1;
      }
    }
  }

  clickWeapon() {
    this.clickWeaponEmit.emit(this.weapon);
  }

  ngOnChanges() {
    this.ngOnInit();
  }
}
