import { Component, OnInit } from '@angular/core';
import { Select, Store } from '@ngxs/store';
import { Weapon, WeaponDetailed, WeaponGrid, WeaponSkillQuery } from 'src/app/models/weapon.model';
import { WeaponGridState } from './../../state/weapon-grid.state';
import { WeaponGridActions } from './../../state/weapon-grid.actions';
import { Observable } from 'rxjs';

@Component({
  selector: 'app-vanguard-calculate',
  templateUrl: './vanguard-calculate.component.html',
  styleUrls: ['./vanguard-calculate.component.css']
})
export class VanguardCalculateComponent implements OnInit {
  @Select(WeaponGridState.getWeaponGrids) grids$: Observable<WeaponGrid[]>;

  pdefMax: number = 1;
  mdefMax: number = 1;
  oppPDef: number = 0;
  oppMDef: number = 0;
  combo: number = 0;
  grids: WeaponGrid[];

  constructor(private store: Store) { }

  ngOnInit(): void {
    this.grids$.subscribe(grids => {
      this.grids = grids;
      this.pdefMax = 1;
      this.mdefMax = 1;

      for (const grid of this.grids) {
        if (grid.stats.patk > ((2 / 3 ) * this.pdefMax)) {
          this.pdefMax = Math.round((3 / 2) * grid.stats.patk);
        }

        if (grid.stats.matk > ((2 / 3 ) * this.mdefMax)) {
          this.mdefMax = Math.round((3 / 2) * grid.stats.matk);
        }
      }

      if (this.oppPDef > this.pdefMax) {
        this.oppPDef = 0;
      }

      if (this.oppMDef > this.mdefMax) {
        this.oppMDef = 0;
      }
    });
  }

  calculate() {
    let i = 0;
    let supMod;

    for (const grid of this.grids) {
      this.store.dispatch(new WeaponGridActions.UpdateGridBase(i, 0));
      this.store.dispatch(new WeaponGridActions.UpdateGridSup(i, 0));
      this.store.dispatch(new WeaponGridActions.UpdateGridAoe(i, 0));
      this.store.dispatch(new WeaponGridActions.UpdateGridTotal(i, 0));
      supMod = this.calculateSupport(grid);

      for (const weapon of grid.weapons) {
        this.calculateWeapon(weapon, grid, i, supMod);
      }

      this.calculatePercent(grid, i);

      i++;
    }
  }

  calculateWeapon(weapon: WeaponDetailed, grid: WeaponGrid, gridNum, supMod) {
    let weaponMod;
    let weaponSkillLevel = weapon.weaponSkillLevel;
    let weaponSkillMod;
    let targets = weapon.skill.targets;
    let weaponType = weapon.weaponInfo.weaponType;
    let job = grid.job;
    let patk = grid.stats.patk;
    let matk = grid.stats.matk;
    let combo = this.combo;
    let comboMod = 1;
    let pdef = this.oppPDef;
    let mdef = this.oppMDef;
    let jobMod;
    let shinmaMod;
    let atk;
    let def;
    let base;
    let sup;
    let aoe;
    let total;

    if (weaponType === 'Sword' || weaponType === 'Hammer') {
      atk = patk;
      def = pdef;
      weaponMod = weapon.skill.patkMod;
    } else {
      atk = matk;
      def = mdef;
      weaponMod = weapon.skill.matkMod;
    }

    if (combo > 1 && combo < 201) {
      comboMod = 1 + (combo * 0.0007);
    } else if (combo > 1 && combo < 501) {
        comboMod = 1 + ((200 * 0.0007) + ((combo - 200) * 0.0005));
    } else if (combo > 1 && combo < 1001) {
        comboMod = 1 + ((200 * 0.0007) + (300 * 0.0005) + ((combo - 500) * 0.00035));
    }

    jobMod = this.getJobMod(job, weaponType);

    shinmaMod = this.getShinmaMod(weaponType, weapon.skill.skillTier, grid.shinma);

    weaponSkillMod = 1 + ((weaponSkillLevel - 1) * 0.025);

    if (weaponSkillLevel > 14) {
      weaponSkillMod += 0.025;
    }

    if (weaponSkillLevel === 20) {
      weaponSkillMod += 0.025;
    }

    base = Number((((atk * comboMod) - (def * (2 / 3))) * (weaponMod * weaponSkillMod * jobMod * shinmaMod * 0.05 * 0.95)).toFixed(0));
    sup = Number((base * supMod).toFixed(0));
    aoe = Number((base * targets).toFixed(0));
    total = Number((base * supMod * targets).toFixed(0));

    this.store.dispatch(new WeaponGridActions.UpdateWeaponBase(gridNum, base, weapon.weaponInfo.weaponName));
    this.store.dispatch(new WeaponGridActions.UpdateWeaponSup(gridNum, sup, weapon.weaponInfo.weaponName));
    this.store.dispatch(new WeaponGridActions.UpdateWeaponAoe(gridNum, aoe, weapon.weaponInfo.weaponName));
    this.store.dispatch(new WeaponGridActions.UpdateWeaponTotal(gridNum, total, weapon.weaponInfo.weaponName));
    this.store.dispatch(new WeaponGridActions.UpdateGridBase(gridNum, (base + grid.results.base)));
    this.store.dispatch(new WeaponGridActions.UpdateGridSup(gridNum, (sup + grid.results.sup)));
    this.store.dispatch(new WeaponGridActions.UpdateGridAoe(gridNum, (aoe + grid.results.aoe)));
    this.store.dispatch(new WeaponGridActions.UpdateGridTotal(gridNum, (total + grid.results.total)));
  }

  calculateSupport(grid: WeaponGrid) {
    let gridSupProc = grid.supProc;
    let supMod = 1;

    for (const weapon of grid.weapons) {
      let supSkill = weapon.weaponInfo.supportSkill;
      let supSkillLevel = weapon.supportSkillLevel;
      let supSkillLevelMod;
      let procRate;

      if (supSkill > 0) {
        if (supSkillLevel === 20) {
          supSkillLevelMod = 1 + (20 * 0.025);
        } else {
          supSkillLevelMod = 1 + ((supSkillLevel - 1) * 0.025);
        }

        procRate = 0.04 + ((supSkillLevel - 1) * 0.005);

        if (supSkillLevel > 14) {
            procRate += 0.005;
        }

        if (supSkillLevel === 20) {
            procRate += 0.005;
        }

        if (gridSupProc == 0) {
          supMod += (((supSkill * 5) + 5) / 100) * supSkillLevelMod * procRate;
        } else if (weapon.supProc == 1) {
          supMod += (((supSkill * 5) + 5) / 100) * supSkillLevelMod;
        }
      }
    }

    return supMod;
  }

  getJobMod(job, weaponType) {
    switch(job) {
      case 'Breaker': {
        if (weaponType === 'Sword') {
          return 1.1;
        } else if (weaponType === 'Hammer') {
          return 1;
        } else if (weaponType === 'Bow') {
          return 1;
        } else if (weaponType === 'Spear') {
          return 1;
        }
        break;
      }
      case 'Crusher': {
        if (weaponType === 'Sword') {
          return 1;
        } else if (weaponType === 'Hammer') {
          return 1.1;
        } else if (weaponType === 'Bow') {
          return 1;
        } else if (weaponType === 'Spear') {
          return 1;
        }
        break;
      }
      case 'Gunner': {
        if (weaponType === 'Sword') {
          return 1;
        } else if (weaponType === 'Hammer') {
          return 1;
        } else if (weaponType === 'Bow') {
          return 1.1;
        } else if (weaponType === 'Spear') {
          return 1;
        }
        break;
      }
      case 'Paladin': {
        if (weaponType === 'Sword') {
          return 1;
        } else if (weaponType === 'Hammer') {
          return 1;
        } else if (weaponType === 'Bow') {
          return 1;
        } else if (weaponType === 'Spear') {
          return 1.1;
        }
        break;
      }
      case 'HNM Breaker': {
        if (weaponType === 'Sword') {
          return 1.35;
        } else if (weaponType === 'Hammer') {
          return 0.25;
        } else if (weaponType === 'Bow') {
          return 0.25;
        } else if (weaponType === 'Spear') {
          return 1;
        }
        break;
      }
      case 'HNM Crusher': {
        if (weaponType === 'Sword') {
          return 0.25;
        } else if (weaponType === 'Hammer') {
          return 1.35;
        } else if (weaponType === 'Bow') {
          return 1;
        } else if (weaponType === 'Spear') {
          return 0.25;
        }
        break;
      }
      case 'HNM Gunner': {
        if (weaponType === 'Sword') {
          return 0.25;
        } else if (weaponType === 'Hammer') {
          return 1;
        } else if (weaponType === 'Bow') {
          return 1.35;
        } else if (weaponType === 'Spear') {
          return 0.25;
        }
        break;
      }
      case 'HNM Paladin': {
        if (weaponType === 'Sword') {
          return 1;
        } else if (weaponType === 'Hammer') {
          return 0.25;
        } else if (weaponType === 'Bow') {
          return 0.25;
        } else if (weaponType === 'Spear') {
          return 1.35;
        }
        break;
      }
      default: {
        return 1;
        break;
      }
    }
  }

  getShinmaMod(weaponType, tier, shinma) {
    switch(shinma) {
      case 'None': {
        return 1;
        break;
      }
      case 'Sword/Spear': {
        if (weaponType === 'Sword' || weaponType === 'Spear') {
          switch(tier) {
            case 1: {
              return 1.3;
              break;
            }
            case 2: {
              return 1.6;
              break;
            }
            case 3: {
              return 2.9;
              break;
            }
            case 4: {
              return 2.9;
              break;
            }
            default: {
              return 1;
              break;
            }
          }
        } else {
          return 1;
        }
        break;
      }
      case 'Hammer/Bow': {
        if (weaponType === 'Hammer' || weaponType === 'Bow') {
          switch(tier) {
            case 1: {
              return 1.3;
              break;
            }
            case 2: {
              return 1.6;
              break;
            }
            case 3: {
              return 2.9;
              break;
            }
            case 4: {
              return 2.9;
              break;
            }
            default: {
              return 1;
              break;
            }
          }
        } else {
          return 1;
        }
        break;
      }
      default: {
        return 1;
        break;
      }
    }
  }

  calculatePercent(grid: WeaponGrid, gridNum) {
    let total = grid.results.total;

    for (const weapon of grid.weapons) {
      let percent;

      percent = Number(((weapon.results.total / total) * 100).toFixed(2));

      this.store.dispatch(new WeaponGridActions.UpdateWeaponPercent(gridNum, percent, weapon.weaponInfo.weaponName));
    }
  }
}
