import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { WeaponDetailed } from 'src/app/models/weapon.model';
import { Select, Store } from '@ngxs/store';
import { WeaponGridActions } from './../../state/weapon-grid.actions';

@Component({
  selector: 'app-grid-weapon-display',
  templateUrl: './grid-weapon-display.component.html',
  styleUrls: ['./grid-weapon-display.component.css']
})
export class GridWeaponDisplayComponent implements OnInit {
  @Input() weapon: WeaponDetailed;
  @Input() supProc: number;
  @Input() className: string;
  @Output() clickWeaponEmit = new EventEmitter();
  @Output() changeWeaponSkillLevel = new EventEmitter();
  @Output() changeSupportSkillLevel = new EventEmitter();
  @Output() changeSupProc = new EventEmitter();

  skillLevel = [...Array(20).keys()].map((i) => i + 1);
  isChecked:boolean = false;

  constructor(private store: Store) { }

  ngOnInit(): void {
  }

  clickWeapon() {
    this.clickWeaponEmit.emit();
  }

  updateWeaponSkillLevel(value: number) {
    this.changeWeaponSkillLevel.emit({
      value: value,
      weaponName: this.weapon.weaponInfo.weaponName
    });
  }

  updateSupportSkillLevel(value: number) {
    this.changeSupportSkillLevel.emit({
      value: value,
      weaponName: this.weapon.weaponInfo.weaponName
    });
  }

  updateSupProc(value: number) {
    this.changeSupProc.emit({
      value: value,
      weaponName: this.weapon.weaponInfo.weaponName
    });
  }

  update() {
    this.store.dispatch(new WeaponGridActions.Update());
  }
}
