export class Weapon {
  weaponType: string;
  weaponName: string;
  weaponSkill: string;
  supportSkill: number;
  rarity: string;
  icon: string[];
  selected: boolean;
}

export class WeaponSkill {
  weaponType: string;
  skillName: string;
  skillTier: number;
  patkMod: number;
  matkMod: number;
  pdefMod: number;
  mdefMod: number;
  healMod: number;
  targets: number;
}

export class WeaponResults {
  base: number = 0;
  sup: number = 0;
  aoe: number = 0;
  total: number = 0;
  percent: number = 0;
  supContr: number = 0;
  supPercent: number = 0;
}

export class WeaponDetailed {
  weaponInfo: Weapon;
  skill: WeaponSkill;
  selectedRarity: string;
  supProc: number = 0;
  weaponSkillLevel: number = 1;
  supportSkillLevel: number = 1;
  results: WeaponResults = new WeaponResults();
}

export class WeaponSkillQuery {
  weaponName: string;
  weaponSkill: string;
  weaponType: string;
}

export class WeaponGrid {
  weapons: WeaponDetailed[] = [];
  supProc: number = 0;
  shinma: string = 'None';
  job: string;
  ready: boolean = false;
  results: GridResults = new GridResults();
  stats: GridStats = new GridStats();
}

export class GridStats {
  patk: number;
  matk: number;
  pdef: number;
  mdef: number;
}

export class GridResults {
  base: number = 0;
  aoe: number = 0;
  sup: number = 0;
  total: number = 0;
}

