import { Injectable } from '@angular/core';
import { Weapon, WeaponSkill, WeaponSkillQuery } from './../models/weapon.model';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Observable } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class WeaponSkillsService {
  weaponSkillsUrl = 'https://sinocalcapi.herokuapp.com/skill';

  constructor(private http: HttpClient) { }

  getWeaponSkills(weaponSkillsQuery: WeaponSkillQuery[]): Observable<WeaponSkill[]> {
    return this.http.post<WeaponSkill[]>(this.weaponSkillsUrl, weaponSkillsQuery);
  }
}
