import { Component, Input, OnInit, Inject } from '@angular/core';
import { WeaponListActions } from './../../state/weapon-list.actions';
import { WeaponListState } from './../../state/weapon-list.state';
import { WeaponGridActions } from './../../state/weapon-grid.actions';
import { WeaponGridState } from './../../state/weapon-grid.state';
import { Select, Store } from '@ngxs/store';
import { Weapon, WeaponGrid, WeaponSkillQuery } from 'src/app/models/weapon.model';
import { Observable } from 'rxjs';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material/dialog';

@Component({
  selector: 'app-weapon-select',
  templateUrl: './weapon-select.component.html',
  styleUrls: ['./weapon-select.component.css']
})
export class WeaponSelectComponent implements OnInit {
  @Select(WeaponListState.getWeaponList) weapons$: Observable<Weapon[]>;
  @Select(WeaponGridState.getWeaponGrids) grids$: Observable<WeaponGrid[]>;

  gridNum:number;
  rarity: string = '';
  weaponType: string = '';
  numSelected: number = 0;
  grid: WeaponGrid;
  weapons: Weapon[];

  constructor(@Inject(MAT_DIALOG_DATA) public data: any, private store: Store) { }

  ngOnInit(): void {
    this.gridNum = this.data.gridNum;

    this.grids$.subscribe(grids => {
      this.grid = grids[this.gridNum];
      if (this.grid) {
        this.numSelected = this.grid.weapons.length;
      }
    });

    this.weapons$.subscribe(weapons => {
      this.weapons = weapons;
    });
  }

  clickWeapon(weapon: Weapon) {
    if (!weapon.selected && this.numSelected < 20) {
      weapon.selected = true;
      this.store.dispatch(new WeaponGridActions.AddToGrid(weapon, this.rarity, this.gridNum));
    } else if (weapon.selected) {
      weapon.selected = false;
      this.store.dispatch(new WeaponGridActions.DeleteFromGrid(weapon.weaponName, this.gridNum));
    }
  }

  saveGrid() {
    const payload = [];

    for (const weapon of this.grid.weapons) {
      const weaponSkillQuery = {
        weaponName: weapon.weaponInfo.weaponName,
        weaponSkill: weapon.weaponInfo.weaponSkill,
        weaponType: weapon.weaponInfo.weaponType,
      };

      payload.push(weaponSkillQuery);
    }

    this.store.dispatch(new WeaponGridActions.GetWeaponSkills(payload, this.gridNum));
    this.store.dispatch(new WeaponListActions.ClearWeaponSelect());
    this.store.dispatch(new WeaponGridActions.GridStateChange(true, this.gridNum));
  }

  importJSON(event) {
    const files = event.target.files[0];

    if (files.length <= 0) {
        return false;
    }

    const fileReader = new FileReader();

    fileReader.readAsText(files, 'UTF-8');
    fileReader.onload = (event) => {
      const grid = JSON.parse(event.target.result.toString());
      const weapons = grid.weapons;

      this.store.dispatch(new WeaponListActions.ClearWeaponSelect());
      this.store.dispatch(new WeaponGridActions.DeleteGrid(this.gridNum));
      this.store.dispatch(new WeaponGridActions.AddGrid());
      this.store.dispatch(new WeaponListActions.SelectWeapons(grid));

      this.store.dispatch(new WeaponGridActions.UpdatePAtk(this.gridNum, grid.patk));
      this.store.dispatch(new WeaponGridActions.UpdateMAtk(this.gridNum, grid.matk));
      this.store.dispatch(new WeaponGridActions.UpdatePDef(this.gridNum, grid.pdef));
      this.store.dispatch(new WeaponGridActions.UpdateMDef(this.gridNum, grid.mdef));
      this.store.dispatch(new WeaponGridActions.UpdateJob(this.gridNum, grid.job));

      this.numSelected = 0;

      for (const listWeapon of this.weapons) {
        if (listWeapon.selected) {
          let selectedIndex = 0;

          for (const weapon of weapons) {
            if (listWeapon.weaponName === weapon.weaponInfo.weaponName) {
              break;
            }

            selectedIndex++;
          }

          this.store.dispatch(new WeaponGridActions.AddToGrid(listWeapon, weapons[selectedIndex].selectedRarity, this.gridNum));
          this.grid.weapons[this.numSelected - 1].weaponSkillLevel = weapons[selectedIndex].weaponSkillLevel;
          this.grid.weapons[this.numSelected - 1].supportSkillLevel = weapons[selectedIndex].supportSkillLevel;
        }
      }
    };
    fileReader.onerror = (error) => {
      console.log(error);
    };
  }
}
