import { Component, Input, OnInit } from '@angular/core';
import { WeaponSelectComponent } from '../weapon-select/weapon-select.component';
import { WeaponGridActions } from './../../state/weapon-grid.actions';
import { WeaponGridState } from './../../state/weapon-grid.state';
import { WeaponListActions } from './../../state/weapon-list.actions';
import { Select, Store } from '@ngxs/store';
import { Observable } from 'rxjs';
import { MatDialog } from '@angular/material/dialog';


@Component({
  selector: 'app-add-grid-modal',
  templateUrl: './add-grid-modal.component.html',
  styleUrls: ['./add-grid-modal.component.css']
})
export class AddGridModalComponent implements OnInit {
  @Select(WeaponGridState.getNumGrids) numGrid$: Observable<number>;
  @Input() classWeapons: string[];

  gridNum: number;
  showCalculate: boolean = false;;

  constructor(public dialog: MatDialog, private store: Store) { }

  ngOnInit(): void {
    this.numGrid$.subscribe(numGrids => {
      if (numGrids < 1) {
        this.showCalculate = false;
      }

      this.gridNum = numGrids - 1;
    });
  }

  addGrid() {
    this.store.dispatch(new WeaponGridActions.AddGrid());

    const dialogRef = this.dialog.open(WeaponSelectComponent, {
      data: {
        classWeapons: this.classWeapons,
        gridNum: this.gridNum
      },
      panelClass: 'weaponSelectContainer',
    });

    dialogRef.afterClosed().subscribe(result => {
      this.showCalculate = true;
      if (!result) {
        this.store.dispatch(new WeaponGridActions.DeleteGrid(this.gridNum));
        this.store.dispatch(new WeaponListActions.ClearWeaponSelect());
      }
    });
  }
}
