import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { WeaponGrid, Classes } from 'src/app/models/weapon.model';
import { WeaponSelectComponent } from '../weapon-select/weapon-select.component';
import { WeaponGridActions } from './../../state/weapon-grid.actions';
import { WeaponListActions } from './../../state/weapon-list.actions';
import { Select, Store } from '@ngxs/store';
import { MatDialog } from '@angular/material/dialog';

@Component({
  selector: 'app-grid-display',
  templateUrl: './grid-display.component.html',
  styleUrls: ['./grid-display.component.css']
})
export class GridDisplayComponent implements OnInit {
  @Input() grid: WeaponGrid;
  @Input() gridNum: number;
  @Input() classType: any;

  shinma: string[];
  className: string;
  jobs: string[];

  constructor(public dialog: MatDialog, private store: Store) { }

  ngOnInit(): void {
    this.shinma = this.classType.shinma;
    this.className = this.classType.name;
    this.jobs = this.classType.jobs;
    this.store.dispatch(new WeaponGridActions.UpdateJob(this.gridNum, this.jobs[0]));
  }

  changeGrid() {
    this.store.dispatch(new WeaponListActions.SelectWeapons(this.grid));

    const dialogRef = this.dialog.open(WeaponSelectComponent, {
      data: {
        classWeapons: this.classType.weapons,
        gridNum: this.gridNum
      },
      panelClass: 'weaponSelectContainer',
    });

    dialogRef.afterClosed().subscribe(result => {
      if (!result) {
        this.store.dispatch(new WeaponListActions.ClearWeaponSelect());
      }
    });
  }

  updatePAtk(value) {
    this.store.dispatch(new WeaponGridActions.UpdatePAtk(this.gridNum, value));
  }

  updateMAtk(value) {
    this.store.dispatch(new WeaponGridActions.UpdateMAtk(this.gridNum, value));
  }

  updatePDef(value) {
    this.store.dispatch(new WeaponGridActions.UpdatePDef(this.gridNum, value));
  }

  updateMDef(value) {
    this.store.dispatch(new WeaponGridActions.UpdateMDef(this.gridNum, value));
  }

  updateWeaponSkillLevel(payload) {
    this.store.dispatch(new WeaponGridActions.UpdateWeaponSkillLevel(this.gridNum, payload.value, payload.weaponName));
  }

  updateSupportSkillLevel(payload) {
    this.store.dispatch(new WeaponGridActions.UpdateSupportSkillLevel(this.gridNum, payload.value, payload.weaponName));
  }

  updateSupProc(payload) {
    this.store.dispatch(new WeaponGridActions.UpdateWeaponSupProc(this.gridNum, payload.value, payload.weaponName));
  }

  updateShinma(value) {
    this.store.dispatch(new WeaponGridActions.UpdateGridShinma(this.gridNum, value));
  }

  updateJob(value) {
    this.store.dispatch(new WeaponGridActions.UpdateJob(this.gridNum, value));
  }

  updateGridSupProc(value) {
    this.store.dispatch(new WeaponGridActions.UpdateGridSupProc(this.gridNum, value));
  }

  deleteGrid() {
    this.store.dispatch(new WeaponGridActions.DeleteGrid(this.gridNum));
  }

  update() {
    this.store.dispatch(new WeaponGridActions.Update());
  }

  exportToJsonFile() {
    const jsonData = this.createExportObject();
    const dataStr = JSON.stringify(jsonData);
    const dataUri = 'data:application/json;charset=utf-8,' + encodeURIComponent(dataStr);
    const exportFileDefaultName = 'weapongrid.json';

    const linkElement = document.createElement('a');
    linkElement.setAttribute('href', dataUri);
    linkElement.setAttribute('download', exportFileDefaultName);
    linkElement.click();
  }

  createExportObject() {
    const grid = this.grid;
    const content = {
      weapons: [],
      patk: grid.stats.patk,
      matk: grid.stats.matk,
      pdef: grid.stats.pdef,
      mdef: grid.stats.mdef,
      job: grid.job
    };

    for (const weapon of grid.weapons) {
      let payload = {
        weaponInfo: {
          weaponName: weapon.weaponInfo.weaponName
        },
        weaponSkillLevel: weapon.weaponSkillLevel,
        supportSkillLevel: weapon.supportSkillLevel,
        selectedRarity: weapon.selectedRarity
      }

      content.weapons.push(payload);
    }

    return content;
  }
}
