import { State, Action, StateContext, Selector } from '@ngxs/store';
import { WeaponListService } from './../services/weapon-list.service';
import { Injectable } from '@angular/core';
import { tap } from 'rxjs/operators';
import {
  Weapon,
  WeaponDetailed,
  WeaponGrid
} from './../models/weapon.model';
import { WeaponListActions } from './weapon-list.actions';

export class WeaponListStateModel {
  weapons: Weapon[];
}

@State<WeaponListStateModel>({
  name: 'weaponList',
  defaults: {
    weapons: []
  }
})

@Injectable()
export class WeaponListState {
  constructor(
    private service: WeaponListService
  ) {}

  @Selector()
  static getWeaponList({ weapons }: WeaponListStateModel) {
    return weapons;
  }

  @Action(WeaponListActions.LoadWeaponList)
  loadWeaponList({ dispatch, getState, patchState }: StateContext<WeaponListStateModel>) {
    return this.service.getWeapons().pipe(
      tap((result) => {
        patchState({
          weapons: result
        });
      })
    );
  }

  @Action(WeaponListActions.ClearWeaponSelect)
  clearWeaponSelect({ dispatch, getState, patchState }: StateContext<WeaponListStateModel>) {
    const state = getState();

    for (const weapon of state.weapons) {
      weapon.selected = false;
    }

    patchState({
      weapons: [...state.weapons]
    });
  }

  @Action(WeaponListActions.SelectWeapons)
  selectWeapons({ dispatch, getState, patchState }: StateContext<WeaponListStateModel>, { grid }: WeaponListActions.SelectWeapons) {
    const state = getState();

    for (const weapon of state.weapons) {
      for (const gridWeapon of grid.weapons) {
        if (weapon.weaponName == gridWeapon.weaponInfo.weaponName) {
          weapon.selected = true;
        }
      }
    }

    patchState({
      weapons: [...state.weapons]
    });
  }
}
