import { Injectable } from '@angular/core';
import { State, Action, StateContext, Selector, StateStream } from '@ngxs/store';
import { WeaponSkillsService } from './../services/weapon-skills.service';
import {
  Weapon,
  WeaponDetailed,
  WeaponGrid
} from './../models/weapon.model';
import { tap } from 'rxjs/operators';
import { WeaponGridActions } from './weapon-grid.actions';
import { patch, updateItem } from '@ngxs/store/operators';
import { WeaponSelectComponent } from '../shared/weapon-select/weapon-select.component';

export class WeaponGridListStateModel {
  numGrid: number;
  grids: WeaponGrid[];
}

@State<WeaponGridListStateModel>({
  name: 'weaponGrids',
  defaults: {
    numGrid: 0,
    grids: []
  }
})

@Injectable()
export class WeaponGridState {
  constructor(
    private service: WeaponSkillsService
  ) {}

  @Selector()
  static getWeaponGrids({ grids }: WeaponGridListStateModel) {
    return grids;
  }

  @Selector()
  static getNumGrids({ numGrid }: WeaponGridListStateModel) {
    return numGrid;
  }

  @Action(WeaponGridActions.AddGrid)
  addGrid({ dispatch, getState, patchState }: StateContext<WeaponGridListStateModel>) {
    const state = getState();

    patchState({
      numGrid: ++state.numGrid,
      grids: [...state.grids, new WeaponGrid()]
    });
  }

  @Action(WeaponGridActions.DeleteGrid)
  deleteGrid({ dispatch, getState, patchState }: StateContext<WeaponGridListStateModel>, { gridNum }: WeaponGridActions.DeleteGrid) {
    const state = getState();

    patchState({
      numGrid: --state.numGrid,
      grids: state.grids.filter(a => a !== state.grids[gridNum])
    });
  }

  @Action(WeaponGridActions.AddToGrid)
  addToGrid({ dispatch, getState, patchState }: StateContext<WeaponGridListStateModel>, { gridNum, selectedRarity, weapon }: WeaponGridActions.AddToGrid) {
    const state = getState();
    const grid = state.grids[gridNum];
    const weaponDetailed = new WeaponDetailed();

    weaponDetailed.weaponInfo = weapon;
    weaponDetailed.selectedRarity = selectedRarity;
    grid.weapons.push(weaponDetailed);

    patchState({
      grids: [...state.grids]
    });
  }

  @Action(WeaponGridActions.DeleteFromGrid)
  deleteFromGrid({ dispatch, getState, patchState }: StateContext<WeaponGridListStateModel>, { gridNum, weaponName }: WeaponGridActions.DeleteFromGrid) {
    const state = getState();
    const grid = state.grids[gridNum];

    grid.weapons = grid.weapons.filter(weapon => weapon.weaponInfo.weaponName !== weaponName);
    patchState({
      grids: [...state.grids]
    });
  }

  @Action(WeaponGridActions.GetWeaponSkills)
  getWeaponSkills({ dispatch, getState, patchState }: StateContext<WeaponGridListStateModel>, { gridNum, weaponSkillQuery }: WeaponGridActions.GetWeaponSkills) {
    const state = getState();
    const weapons = state.grids[gridNum].weapons;

    return this.service.getWeaponSkills(weaponSkillQuery).pipe(
      tap((result) => {

        for (let i = 0; i < weapons.length; i++) {
          weapons[i].skill = result[i];
        }

        patchState({
          grids: [...state.grids]
        });
      })
    );
  }

  @Action(WeaponGridActions.GridStateChange)
  gridChangeState({ dispatch, getState, patchState }: StateContext<WeaponGridListStateModel>, { flag, gridNum }: WeaponGridActions.GridStateChange) {
    const state = getState();
    state.grids[gridNum].ready = flag;

    patchState({
      grids: [...state.grids]
    });
  }

  @Action(WeaponGridActions.UpdatePAtk)
  updatePAtk({ dispatch, getState, patchState }: StateContext<WeaponGridListStateModel>, { gridNum, value }: WeaponGridActions.UpdatePAtk) {
    const state = getState();
    const grid = state.grids[gridNum];

    grid.stats.patk = value;

    patchState({
      grids: [...state.grids]
    });
  }

  @Action(WeaponGridActions.UpdateMAtk)
  updateMAtk({ dispatch, getState, patchState }: StateContext<WeaponGridListStateModel>, { gridNum, value }: WeaponGridActions.UpdateMAtk) {
    const state = getState();
    const grid = state.grids[gridNum];

    grid.stats.matk = value;

    patchState({
      grids: [...state.grids]
    });
  }

  @Action(WeaponGridActions.UpdatePDef)
  updatePDef({ dispatch, getState, patchState }: StateContext<WeaponGridListStateModel>, { gridNum, value }: WeaponGridActions.UpdatePDef) {
    const state = getState();
    const grid = state.grids[gridNum];

    grid.stats.pdef = value;

    patchState({
      grids: [...state.grids]
    });
  }

  @Action(WeaponGridActions.UpdateMDef)
  updateMDef({ dispatch, getState, patchState }: StateContext<WeaponGridListStateModel>, { gridNum, value }: WeaponGridActions.UpdateMDef) {
    const state = getState();
    const grid = state.grids[gridNum];

    grid.stats.mdef = value;

    patchState({
      grids: [...state.grids]
    });
  }

  @Action(WeaponGridActions.UpdateWeaponSkillLevel)
  updateWeaponSkillLevel({ dispatch, getState, patchState }: StateContext<WeaponGridListStateModel>, { gridNum, value, weaponName }: WeaponGridActions.UpdateWeaponSkillLevel) {
    const state = getState();
    const grid = state.grids[gridNum];
    const weapon = grid.weapons.filter(weapon => weapon.weaponInfo.weaponName === weaponName);

    weapon[0].weaponSkillLevel = value;

    patchState({
      grids: [...state.grids]
    });
  }

  @Action(WeaponGridActions.UpdateSupportSkillLevel)
  updateSupportSkillLevel({ dispatch, getState, patchState }: StateContext<WeaponGridListStateModel>, { gridNum, value, weaponName }: WeaponGridActions.UpdateSupportSkillLevel) {
    const state = getState();
    const grid = state.grids[gridNum];
    const weapon = grid.weapons.filter(weapon => weapon.weaponInfo.weaponName === weaponName);

    weapon[0].supportSkillLevel = value;

    patchState({
      grids: [...state.grids]
    });
  }

  @Action(WeaponGridActions.UpdateWeaponSupProc)
  updateWeaponSupProc({ dispatch, getState, patchState }: StateContext<WeaponGridListStateModel>, { gridNum, value, weaponName }: WeaponGridActions.UpdateWeaponSupProc) {
    const state = getState();
    const grid = state.grids[gridNum];
    const weapon = grid.weapons.filter(weapon => weapon.weaponInfo.weaponName === weaponName);

    weapon[0].supProc = value;

    patchState({
      grids: [...state.grids]
    });
  }

  @Action(WeaponGridActions.UpdateGridShinma)
  updateGridShinma({ dispatch, getState, patchState }: StateContext<WeaponGridListStateModel>, { gridNum, shinma }: WeaponGridActions.UpdateGridShinma) {
    const state = getState();
    const grid = state.grids[gridNum];

    grid.shinma = shinma;

    patchState({
      grids: [...state.grids]
    });
  }

  @Action(WeaponGridActions.UpdateGridSupProc)
  updateGridSupProc({ dispatch, getState, patchState }: StateContext<WeaponGridListStateModel>, { gridNum, value }: WeaponGridActions.UpdateGridSupProc) {
    const state = getState();
    const grid = state.grids[gridNum];

    grid.supProc = value;

    patchState({
      grids: [...state.grids]
    });
  }

  @Action(WeaponGridActions.UpdateJob)
  updateJob({ dispatch, getState, patchState }: StateContext<WeaponGridListStateModel>, { gridNum, value }: WeaponGridActions.UpdateJob) {
    const state = getState();
    const grid = state.grids[gridNum];

    grid.job = value;

    patchState({
      grids: [...state.grids]
    });
  }

  @Action(WeaponGridActions.UpdateWeaponBase)
  updateWeaponBase({ dispatch, getState, patchState }: StateContext<WeaponGridListStateModel>, { gridNum, value, weaponName }: WeaponGridActions.UpdateWeaponBase) {
    const state = getState();
    const grid = state.grids[gridNum];
    const weapon = grid.weapons.filter(weapon => weapon.weaponInfo.weaponName === weaponName);

    weapon[0].results.base = value;

    patchState({
      grids: [...state.grids]
    });
  }

  @Action(WeaponGridActions.UpdateWeaponAoe)
  updateWeaponAoe({ dispatch, getState, patchState }: StateContext<WeaponGridListStateModel>, { gridNum, value, weaponName }: WeaponGridActions.UpdateWeaponAoe) {
    const state = getState();
    const grid = state.grids[gridNum];
    const weapon = grid.weapons.filter(weapon => weapon.weaponInfo.weaponName === weaponName);

    weapon[0].results.aoe = value;

    patchState({
      grids: [...state.grids]
    });
  }

  @Action(WeaponGridActions.UpdateWeaponSup)
  updateWeaponSup({ dispatch, getState, patchState }: StateContext<WeaponGridListStateModel>, { gridNum, value, weaponName }: WeaponGridActions.UpdateWeaponSup) {
    const state = getState();
    const grid = state.grids[gridNum];
    const weapon = grid.weapons.filter(weapon => weapon.weaponInfo.weaponName === weaponName);

    weapon[0].results.sup = value;

    patchState({
      grids: [...state.grids]
    });
  }

  @Action(WeaponGridActions.UpdateWeaponTotal)
  updateWeaponTotal({ dispatch, getState, patchState }: StateContext<WeaponGridListStateModel>, { gridNum, value, weaponName }: WeaponGridActions.UpdateWeaponTotal) {
    const state = getState();
    const grid = state.grids[gridNum];
    const weapon = grid.weapons.filter(weapon => weapon.weaponInfo.weaponName === weaponName);

    weapon[0].results.total = value;

    patchState({
      grids: [...state.grids]
    });
  }

  @Action(WeaponGridActions.UpdateWeaponPercent)
  updateWeaponPercent({ dispatch, getState, patchState }: StateContext<WeaponGridListStateModel>, { gridNum, value, weaponName }: WeaponGridActions.UpdateWeaponPercent) {
    const state = getState();
    const grid = state.grids[gridNum];
    const weapon = grid.weapons.filter(weapon => weapon.weaponInfo.weaponName === weaponName);

    weapon[0].results.percent = value;

    patchState({
      grids: [...state.grids]
    });
  }

  @Action(WeaponGridActions.UpdateGridBase)
  updateGridBase({ dispatch, getState, patchState }: StateContext<WeaponGridListStateModel>, { gridNum, value }: WeaponGridActions.UpdateGridBase) {
    const state = getState();
    const grid = state.grids[gridNum];

    grid.results.base = value;

    patchState({
      grids: [...state.grids]
    });
  }

  @Action(WeaponGridActions.UpdateGridAoe)
  updateGridAoe({ dispatch, getState, patchState }: StateContext<WeaponGridListStateModel>, { gridNum, value }: WeaponGridActions.UpdateGridAoe) {
    const state = getState();
    const grid = state.grids[gridNum];

    grid.results.aoe = value;

    patchState({
      grids: [...state.grids]
    });
  }

  @Action(WeaponGridActions.UpdateGridSup)
  updateGridSup({ dispatch, getState, patchState }: StateContext<WeaponGridListStateModel>, { gridNum, value }: WeaponGridActions.UpdateGridSup) {
    const state = getState();
    const grid = state.grids[gridNum];

    grid.results.sup = value;

    patchState({
      grids: [...state.grids]
    });
  }

  @Action(WeaponGridActions.UpdateGridTotal)
  updateGridTotal({ dispatch, getState, patchState }: StateContext<WeaponGridListStateModel>, { gridNum, value }: WeaponGridActions.UpdateGridTotal) {
    const state = getState();
    const grid = state.grids[gridNum];

    grid.results.total = value;

    patchState({
      grids: [...state.grids]
    });
  }

  @Action(WeaponGridActions.Update)
  update({ dispatch, getState, patchState }: StateContext<WeaponGridListStateModel>) {
    const state = getState();

    patchState({
      grids: [...state.grids]
    });
  }
}
